form {
  display: flex;
  flex-direction: column;
}

form button {
  margin: 0px auto;
  margin-top: 10px;
  padding: 7px 10px;

  display: block;

  font-size: 16px;
  color: #726e6e;
  background-color: rgb(154, 233, 35);

  border: 1px solid transparent;
  border-radius: 5px;

  transition: all 0.5s ease;
}

form button:hover {
  color: rgb(154, 233, 35);
  background-color: transparent;
  border-color: rgb(154, 233, 35);
}

form input {
  padding: 10px;
  margin-bottom: 10px;

  font-family: inherit;
  font-weight: 700;
  color: #726e6e;

  border: 2px solid transparent;
  border-radius: 5px;
  outline: none;
}

form input:focus {
  border-color: rgb(154, 233, 35);
}

form input[type="checkbox"] {
  margin-left: 5px;
}

form input::placeholder {
  font-size: 15px;
}

label {
  padding-left: 15px;
}

.user__item form {
  margin-top: 20px;
}

.user__item form input {
  background-color: #134b57;
  color: #e4e1e1;
}

.user__item form input::placeholder {
  color: rgb(183, 192, 183);
}
