@import url(../css/var.css);

.users {
  margin-left: var(--indent50);
}
.user__list {
  list-style: none;
}
.user__item {
  padding: 15px;

  width: 70%;

  background-color: #3e8fa0;
  border-radius: 3px;
}

.user-container {
  display: flex;
  justify-content: space-between;
}

.user__item:not(:last-child) {
  margin-bottom: var(--indent20);
}

.user__title {
  margin-top: var(--indent10);
  margin-bottom: var(--indent10);
}
.user__bio {
  margin-bottom: var(--indent10);
}

.user-icons__edit {
  margin-right: 20px;

  color: rgb(9, 236, 9);

  transform: scale(1.5);
  cursor: pointer;
  opacity: 0.5;
  transition: all 0.5s ease;
}
.user-icons__delete {
  color: rgb(247, 17, 17);

  transform: scale(1.5);
  cursor: pointer;
  opacity: 0.5;
  transition: all 0.5s ease;
}

.user-icons__edit:hover,
.user-icons__delete:hover {
  opacity: 1;
}
